<template>
  <div class="contacts cont" ref="contacts">
    <button class="cont__burger" 
      :style="{ top: position, backgroundImage: `url(${icon})` }"
      @click="clicked"
    ></button>
    <div class="cont__wrap" v-if="isOpen">
      <transition-group name="contacts" appear>
        <mail :key="1" style="--i: 1"></mail>
        <git :key="2" style="--i: 2"></git>
        <telega :key="3" style="--i: 3"></telega>
        <phone :key="4" style="--i: 4"></phone>
      </transition-group>
    </div>
  </div>
</template>

<script>
import mail from '@/components/contacts/contact-gmail.vue'
import git from '@/components/contacts/contact-github.vue'
import telega from '@/components/contacts/contact-telega.vue'
import phone from '@/components/contacts/contact-phone.vue'
export default {
  name: 'head-section-contacts',
  components: { mail, git, telega, phone },
  data() {
    return {
      coord: '',
      isOpen: true,
    }
  },
  computed: {
    position() { return this.coord - 70 + 'px' },
    icon() { return this.isOpen ? this.url('cross.svg') : this.url('burger.svg') },
  },
  methods: {
    url(el) { return require('@/assets/svg/' + el) },
    clicked() {
      this.isOpen = !this.isOpen
      this.$emit('clicked') 
    },
    getCoord(el) { return el.getBoundingClientRect().y }
  },
  mounted() {
    const el = this.$refs.contacts
    this.coord = this.getCoord(el)
    if (el) window.addEventListener('resize', () => {
        this.coord = this.getCoord(el)
        this.$emit('resize', el.getBoundingClientRect().width)
      }
    )
  }
}
</script>

<style lang="scss" scoped>
.contacts, .cont {
  flex: 1;
  &__burger {
    width: 36px;
    height: 36px;
    background-repeat: no-repeat;
    background-position: center;
    @include media('min', 'xs') { display: block; }
    @include media('min', 'md') { display: none; }
    position: absolute;
    left: calc(50% - 18px);
    transition: all 0.3s ease;
  } 
  &__wrap {
    background-color: $grey-80;
    @include media('min', 'md') {
      width: 100%;
      @include fr;
      padding: 0;
      margin: 0;

      position: absolute;      
      left: 0;
      right: 0;
      bottom: -34px;
    }
    @include media('min', 'lg') {
      position: static;
      @include fr;
      margin: 0;
    }
  }

  .contacts-enter-active {
    transition: all 0.4s ease;
    transition-delay: calc(0.1s * var(--i));
  }
  .contacts-enter-from {
    transform: scale(0.5);
    opacity: 0.5;
  }

}
</style>